import React, { Component, Fragment } from "react";
import { IoMdHelp } from "react-icons/io";
import { MdClose, MdPermPhoneMsg } from "react-icons/md";
import { Auth } from "../../actions";
import { SERVER_URL } from "../../config/app.config";
import Modal, { ModalSize } from "../Modal/Modal";
import PdfViewer from "../PdfViewer/PdfViewer";

interface SupportPopupProps {
  auth: Auth;
  setShowSupportTeam: (status: boolean) => void;
  showSupportTeam: boolean;
}
interface SupportPopupState {
  loading: boolean;
  hide: boolean;
}

export class SupportPopup extends Component<
  SupportPopupProps,
  SupportPopupState
> {
  constructor(props: SupportPopupProps) {
    super(props);
    this.state = {
      loading: false,
      hide: false,
    };
  }
  render() {
    return (
      <Fragment>
        <div className="fixed bottom-2 right-2 flex flex-row items-center gap-2">
          {this.state.hide === false && (
            <div
              onClick={() =>
                this.props.setShowSupportTeam(!this.props.showSupportTeam)
              }
              className="p-1 pr-3 rounded-full shadow-xl flex flex-row items-center justify-between font-bold gap-2 bg-white cursor-pointer border-2 border-yellow-600 hover:border-yellow-800 group animate__animated animate__zoomIn"
            >
              <div className="flex flex-row items-center gap-1">
                <div>
                  <div className="bg-accent-900 group-hover:bg-yellow-800 rounded-full h-10 w-10 flex items-center justify-center text-white animate__animated animate__fadeIn animate__infinite">
                    <MdPermPhoneMsg className="text-2xl" />
                  </div>
                </div>
                <span className="text-lg group-hover:text-yellow-700">
                  Contact Support?
                </span>
              </div>
            </div>
          )}
          {this.state.hide === false ? (
            <div
              className={`${
                this.props.auth.isAuthenticated === true ? "" : "hidden"
              }`}
            >
              <div
                onClick={() => this.setState({ hide: !this.state.hide })}
                className="h-12 w-12 rounded-full bg-red-600 text-white flex items-center justify-center shadow-xl cursor-pointer animate__animated animate__fadeIn animate__slower animate__infinite"
              >
                <MdClose className="text-3xl" />
              </div>
            </div>
          ) : (
            <div
              title="Contact for support"
              className="h-12 w-12 rounded-full flex items-center justify-center bg-yellow-700 text-white cursor-pointer animate__animated animate__fadeInBottomRight shadow-xl"
              onClick={() => this.setState({ hide: !this.state.hide })}
            >
              <IoMdHelp className="text-3xl" />
            </div>
          )}
        </div>
        {this.props.showSupportTeam === true && (
          <Modal
            backDrop={true}
            close={() => this.props.setShowSupportTeam(false)}
            backDropClose={true}
            displayClose={true}
            padding={{
              title: true,
              body: false,
            }}
            widthSizeClass={ModalSize.extraLarge}
            title={
              <div className="font-extrabold text-2xl">TMIS SUPPORT STAFF</div>
            }
          >
            <div className="bg-gray-200 rounded-md min-h-screen -mt-0 m-2">
              {this.state.loading === true ? (
                <div className="bg-gray-100 rounded-md p-3 py-6 flex flex-col items-center justify-center w-full my-4"></div>
              ) : (
                <PdfViewer
                  setLoadingFile={(status: boolean) =>
                    this.setState({ loading: status })
                  }
                  file_url={`${SERVER_URL}/support_staff.pdf`}
                  class_name={"w-full min-h-screen h-full"}
                  frame_title={""}
                />
              )}
            </div>
          </Modal>
        )}
      </Fragment>
    );
  }
}
