interface TrainingValidation__Interface {
  status: boolean;
  errors: string[];
}

export const TrainingValidation__Step1 = (
  training: TrainingInterface | null
): TrainingValidation__Interface => {
  if (!training?.title || training?.title?.length <= 0)
    return {
      errors: ["Title is not provided"],
      status: false,
    };

  if (!training?.description || training?.description?.length <= 0)
    return {
      errors: ["Description is not provided"],
      status: false,
    };

  if (!training?.sub_area_id || training?.sub_area_id?.length <= 0)
    return {
      errors: ["Sub area is not provided"],
      status: false,
    };

  if (!training?.budget || training?.budget === 0)
    return {
      errors: ["Budget is not provided"],
      status: false,
    };

  if (!training?.start_date || training?.start_date.length <= 0)
    return {
      errors: ["Start date is not provided"],
      status: false,
    };

  if (!training?.end_date || training?.end_date.length <= 0)
    return {
      errors: ["End date is not provided"],
      status: false,
    };

  if (
    !training?.trainer_institution ||
    training?.trainer_institution.length <= 0
  )
    return {
      errors: ["Please add a trainer institution"],
      status: false,
    };

  if (!training?.trainee_category || training?.trainee_category.length <= 0)
    return {
      errors: ["Please add a trainer institution"],
      status: false,
    };

  return {
    errors: [],
    status: true,
  };
};

export const TrainingValidation__Step2 = (
  training: TrainingInterface | null
): TrainingValidation__Interface => {
  if (!training?.groups || training?.groups.length <= 0)
    return {
      errors: ["Please Provide a group"],
      status: false,
    };

  if (!training?.trainers || training?.trainers.length <= 0)
    return {
      errors: ["Please add trainers to the groups"],
      status: false,
    };

  if (training?.groups.length > training?.trainers.length)
    return {
      errors: ["Please add a trainer on each group"],
      status: false,
    };

  return {
    errors: [],
    status: true,
  };
};

export const TrainingValidation__Step3 = (
  training: TrainingInterface | null
): TrainingValidation__Interface => {
  return {
    errors: [],
    status: true,
  };
};

export const TrainingValidation__Step4 = (
  training: TrainingInterface | null
): TrainingValidation__Interface => {
  if (!training?.material_links || training?.material_links.length <= 0)
    return {
      errors: ["Please provide material links"],
      status: false,
    };

  if (!training?.agenda_file_name || training?.agenda_file_name.length <= 0)
    return {
      errors: ["Please provide am agenda"],
      status: false,
    };

  return {
    errors: [],
    status: true,
  };
};

export enum TrainingStatus {
  DRAFT = "DRAFT",
  READY_FOR_EVALUATION = "READY_FOR_EVALUATION",
  INPROGRESS = "INPROGRESS",
  COMPLETED = "COMPLETED",
  CANCELLED = "CANCELLED",
}

export enum TrainingStatusREB {
  PENDING = "PENDING",
  APPROVED = "APPROVED",
  REJECTED = "REJECTED",
}

export const FC_ChangeTrainingStatus = async (
  training_id: string,
  status: TrainingStatus,
  callBack: (status: boolean, error: string) => void
) => {
  try {
    setAxiosToken();
    await axios.patch(
      `http://tmis.reb.rw/training/api/training/application/implementation/${training_id}`,
      {
        status: status,
      }
    );
    callBack(true, "");
  } catch (error) {
    callBack(false, errorToText(error));
  }
};

